/* BOX SHADOW */
$box-shadow: 1.75rem 1.5rem 1.75rem $shadow-color;
$box-shadow-sm: 1.25rem 1rem 1.25rem $shadow-color .5;
$box-shadow-lg: 2.25rem 2rem 2.25rem $shadow-color .75;
$box-shadow-hover: .25rem .50rem .75rem $shadow-hover;
$box-shadow-focus: -.25rem .50rem .75rem $shadow-focus, -.25rem -.50rem .75rem tint-color(rgba(50,50,50), 25%);

$box-shadow-inset: inset -.25rem -.25rem -.50rem 0px rgb($shadow-inset-a, .7) 25%, inset .25rem .25rem .50rem 0px rgb($shadow-inset-b, .35) 25%;
